import React from 'react'
import { getPlayListData } from '../../../fetch/home/index.jsx'
import PlayList from '../../../components/HomePlayList'

class HomePlayList extends React.Component{
    constructor(props,context){
        super(props,context);
        this.state={
            data:[]
        }
    }
    render(){
        return(
            <div>
                {
                    this.state.data.length
                    ?<PlayList data={this.state.data}/>
                    : <h5>playList的数据正在请求中</h5>
                }
            </div>
        )
    }
    componentDidMount(){
        // 利用fetch发送ajax请求,请求mock中的数据，得到一个Promise对象
        getPlayListData()
        .then(res=>res.json())
        // 拿到数据之后更新state
        .then(json=>this.setState({data:json}))
        .catch(ex=>console.error('请求playList中的信息出错'+ex.message))
    }
}

export default HomePlayList


