import React from 'react'
import { Link } from 'react-router'
import './style.less'

const arrowImg=require('../../static/images/find/arrow.png')

class HomePlayList extends React.Component{
    constructor(props,context){
        super(props,context);
        this.state={
            data:[]
        }
    }
    onClickHandle(i){
        console.log(i)
    }
    render(){
        return(
            <div className='home-playList'>
                    <Link to='/homePlayList' className='homePlayList-header'>
                        <span>推荐歌单</span>
                        <img src={arrowImg} />
                    </Link>
                    <div className='homePlayList-container'>
                       <ul className='homePlayList-ul'>
                           {this.props.data.map((item,index)=>{
                             return(
                                <li  key={index}>
                                     <Link to={item.link}>
                                        <img src={item.img} alt={item.title}/>
                                        <span>{item.title}</span>
                                     </Link>
                                </li>
                               )
                            })}
                        </ul>
                    </div>
                </div>
        )
    }
}

export default HomePlayList


