import React from 'react'
import { hashHistory } from 'react-router'

import './style.less'
class SearchList extends React.Component{
    constructor(props,context){
        super(props,context);
        this.state={
            data:[]
        }
    }
    render(){
        console.log(this.props.message)
        const message=this.props.message
        return(
            <div className='searchList-container'>
                <ul className='searchList-container-ul'>
                    {message.map((item,index)=>{
                        return(
                            <div  key={index} className='searchList-container-a' onClick={()=>this.onClickHandle(item)}>
                                <li className='searchList-container-li'>
                                    <span className='songName'>{item.songName}</span>
                                    <p className='detail'>{item.detail}</p>
                                    <p className='note'>{item.note}</p>
                                    <img src={item.img} className='sq'/>
                                </li>
                            </div>
                        )
                    })}
                </ul>
                
            </div>
        )
    }
    onClickHandle(all){
        console.log(all)
        this.setState({data:all.songName})
        hashHistory.push(`/play/${all.songName}`)
    }
}
export default SearchList
                                //  {/* <span className='name'>{item.name}</span> */}




