import React from 'react'
import $ from 'jquery'

import './style.less'
// require('./jquery.min/js')

const jiaoPian=require('../../static/images/play/disc.png')
const share=require('../../static/images/play/share.png')
const needle=require('../../static/images/play/needle.png')
const arrowLeft=require('../../static/images/arrow-left.png')
const pause=require('../../static/images/play/btn_pause.png')
const play=require('../../static/images/play/btn_play.png')
const prev=require('../../static/images/play/pre.png')
const next=require('../../static/images/play/next.png')
class PlayPage extends React.Component{
    constructor(props,context){
        super(props,context)
    }
    render(){
        // console.log(this.props.playData[0])
        console.log(this.props.playData)

        const playData=this.props.playData
        return(
                <div className='playPage'>
                        {
                            playData?
                            playData.map((item,i)=>{
                                console.log(this.props.playData[0].bgimg)
                                const bg={
                                    width:'100%',
                                    height:'100%',
                                    backgroundImage:`url(${this.props.playData[0].bgimg})`,
                                                }
                            return(
                            <div className='playPage-div' key={i} >
                                <div className='playPage-bg' style={bg} ></div>
                                <img src={item.coverimg} className='coverImg'/>
                                <img src={jiaoPian} className='jiaopian'/>
                                <img src={needle} className='needle'/>
                                <div className='btn'>
                                    <img src={pause} className='pause'/>
                                    <img src={play} className='play' onClick={this.onChangeHandle.bind(this)}/>
                                </div>
                                <img src={prev} className='prev'/>
                                <img src={next} className='next'/>
                                <div className='singer-div'>
                                    <img src={arrowLeft} className='arrowLeft' onClick={this.onClickHandle.bind(this)}/>
                                    <span className='singer'>{item.singer}</span> 
                                    <img src={share} className='share'/>
                                </div>
                                <div className='audio-div'>
                                    <audio id='audio'>
                                        <source src={item.url} type='audio/mpeg'/>
                                    </audio>
                                </div>
                            </div>
                            )
                        }):''
                        }
                </div>
        )
    }
    onClickHandle(){
        // 改路由
        console.log(111);
        window.history.back()
    }
  
    onChangeHandle(){
        let audio=document.getElementById('audio')
        // console.log(audio)
        audio.oncanplay=function(){
            audio.play()
            $('.btn').addClass('active')
            $('.needle').addClass('active')
        }
        $('.play').on('click',function(){
            audio.play()
            $('.needle').addClass('active')
            $('.btn').addClass('active')
            $('.coverImg').addClass('active')
        })
        $('.pause').on('click',function(){
            audio.pause()
            $('.btn').removeClass('active')
            $('.needle').removeClass('active')
            $('.coverImg').removeClass('active')
        })
    }
}
export default PlayPage


