import React from 'react'
import SearchList from '../../../components/SearchList'

import { getSearch } from '../../../fetch/search/index.jsx'
// import { getSearchDa}
class SubSearchList extends React.Component{
    constructor(props,context){
        super(props,context);
        this.state={
            data:[]
        }
    }
    render(){
        console.log(this.state.data)
        return(
            <div className='searchList'>
                 <SearchList message={this.state.data}/>
            </div>
        )
    }
    getSearchData(){
        getSearch({
            keyWords:this.props.keyWords
        })
        .then(res=>res.json())
        .then(json=>{
            console.log(json)
            this.setState({
                data:json.data
            })
            // console.log(this.state.data)
        })
        .catch(ex=>console.error('搜索信息有误'+ex.message))
    }
    componentDidMount(){
        console.log('componentDidMount执行了')
        this.getSearchData()
    }
    // 在组件接收到一个新的props时调用
    componentWillReceiveProps(nextProps){
        if(this.props.keyWords!=nextProps.keyWords){
            this.setState({data:[]})
        }
    }
}

export default SubSearchList


