import React from 'react'
import { Link } from 'react-router'
import './style.less'

class SingerList extends React.Component{
    constructor(props,context){
        super(props,context);
        // this.onClickHandle=this.onClickHandle.bind(this);
        // this.state={
        //     singerType:['华语男歌手','华语女歌手','华语组合','欧美男歌手',
        //     '欧美女歌手','欧美组合','日本歌手','韩国歌手','影视作品','少儿歌曲','钢琴曲'],
        // }
    }
    // onClickHandle(i,item){
    //     console.log(i);
    //     console.log(item);
    // }
    render(){
        return(
               <ul className='singerListType-left-ul'>
                    {this.props.data.map((item,i)=>{
                        return(
                        <li className='singerListType-left-li' onClick={this.props.onClickHandle.bind(this,i,item)} key={i}>
                            <span>{item}</span>
                        </li>
                     )
                   })}
                </ul>

        )
    }
}
export default SingerList






