import React from 'react'
import { Link } from 'react-router'
import './style.less'



class HomeFind extends React.Component{
    constructor(props,context){
        super(props,context);
        
    }
   render(){

    const fmImg=require('../../static/images/find/fm.png')
    const daySongImg=require('../../static/images/find/daySong.png')
    const playListImg=require('../../static/images/find/playList.png')

        return (
                <div className='home-introduce'>
                    <Link to='/' className='three find-fm'>
                        <div className='find-ico'>
                            <img src={fmImg} />
                        </div>
                        私人FM
                    </Link>
                    <Link to='/daySong' className='three find-daySong'>
                        <div className='find-ico'>
                            <img src={daySongImg}/>
                            <span className='daySong-date'>{(new Date()).getDate()}</span>
                        </div>
                        每日歌曲推荐
                    </Link>
                    <Link to='/' className='three find-playList'>
                        <div className='find-ico'>
                            <img src={playListImg}/>
                        </div>
                        云音乐热歌榜
                  </Link>
                </div>
        )
    }
}


export default HomeFind


