import React from 'react'
import { Link } from 'react-router'
import './style.less'

class SingerList extends React.Component{
    constructor(props,context){
        super(props,context)
    }
    render(){
        const arrow=require('../../static/images/arrow.png')
        // console.log(this.props.listData)
        return(
            <ul className='singerListType-right-ul'>
                {this.props.listData.map((item,index)=>{
                    return(
                        <li  className='singerListType-right-li' key={index}>
                            <img className='person' src={item.images} />
                            <span>{item.person}</span>
                            <img className='arrow'   src={arrow} />
                        </li>
                    )
                })}
            </ul>   
        )
    }
}
export default SingerList


