import React from 'react'
import { Router,Route,IndexRoute,hashHistory,IndexRedirect } from 'react-router'
import Home from '../pages/Home/index.jsx'
import SingerList from '../pages/SingerList'
import DaySong from '../pages/DaySong'
import Rank from '../pages/Rank'
import RiseRank from '../pages/RiseRank'
import Search from '../pages/Search'
import Play from '../pages/Play'
import App from '../pages/index.jsx'

class RouteMap extends React.Component{
    render(){
        // 一个路由只能有一个Router,公用模板<App></App>
        return(
            <Router history={hashHistory}>
                <Route path='/' component={App}>
                    <Route path='/home' component={ Home } />
                    <Route path='/singerList' component={ SingerList }/>
                    <Route path='/rank' component={ Rank } />
                        <Route path='/rank/riseRank' component={RiseRank} />
                    <Route path='/daySong' component={ DaySong} />
                    <Route path='/play/:junk' component={Play} /> 
                    <Route path='/search/:keyWords' component={ Search } />
                    <IndexRedirect  to='/home'/>
                    
                </Route>
            </Router>
        )
    }
}

export default RouteMap




