import React from 'react'
import Left from '../../../components/SingerList/ul-left.jsx'
import Right from '../../../components/SingerList/ul-right.jsx'
import { getSingerList } from '../../../fetch/singerList'

class SingerList extends React.Component{
    constructor(props,context){
        super(props,context);
        this.state={
            data:[],
            hasMore:false,
            isLoadingMore:false,
            singerType:['华语男歌手','华语女歌手','华语组合','欧美男歌手',
            '欧美女歌手','欧美组合','日本歌手','韩国歌手','影视作品','少儿歌曲','钢琴曲'],
            displayList:[],
        };
        // console.log(this.state.singerType);
        
    }
    onClickHandle(i,types){
        // console.log(i)
        // console.log(types);
        var result=this.state.data.filter(all=>all.types===types);
        if(result.length){
            this.setState({displayList:result})
        }else{
            console.log('暂无数据')
        }
    }
    render(){
        // console.log(this.state.displayList)
        return(
            <div className='singerList'>
                <Left  data={this.state.singerType} onClickHandle={this.onClickHandle.bind(this)} />
                <Right listData={this.state.displayList} />
            </div>
        )
    }
    // 发送请求拿数据
    getSingerListData(){
        getSingerList({})
        .then(res=>res.json())
        .then(json=>{
            this.setState({
                data:json.data
            })
        })
        .catch(ex=>console.error('请求mock数据库的数据有误:'+ex.message))
    }
  
    componentDidMount(){
    //    this.getSingerListData();
          this.getSingerListData();
    }
    // shouldComponentUpdate(){
    //     var result=this.state.data.filter(all=>all.types==='华语男歌手');
    //         this.setState({displayList:result}) 
    //         console.log(this.state.displayList)
    // }
    // componentWillUpdate(){
            // var result=this.state.data.filter(all=>all.types==='华语男歌手');
            // this.setState({displayList:result}) 
            // console.log(this.state.displayList)
    // }
}
export default SingerList;

