// app的母版页
// 一个组件：表现任务，数据任务（和redux相关的数据任务）
// 如果是和redux相关的数据任务就放在pages页面中，否则放在components文件夹中
import React from 'react'
import { bindActionCreators } from 'redux'//批量绑定creator
import { connect } from 'react-redux'

class App extends React.Component{
    constructor(props,context){
        super(props,context);
        this.state={
            // 默认一开始拿数据
            isFetching:true
        }
    }
    render(){
        return(
            <div>
                {
                    this.state.isFetching?<h1>正在加载</h1>:<div>{this.props.children}</div>
                }
            </div>
        )
    }
    componentDidMount(){
        this.setState({isFetching:false})
    }
}

//----------------react-redux关联绑定---------------------
function mapStateToProps(state){
    return {}
}

function mapDispatchToProps(dispatch){
    return {   }
}
export default connect(
    mapStateToProps,
    mapDispatchToProps
)(App)


