import React from 'react'
import { Link } from 'react-router'
import './style.less'

class RankList extends React.Component{
    constructor(props,context){
        super(props,context);
        this.state={
            hhh:'官方榜'
        }
    }
    render(){
        console.log(this.props.data)
        return(
           <div className='rankList'>
               <span className='all'>{this.state.hhh}</span>
               <ul className='rankList-ul'>
                   {this.props.data.map((item,index)=>{
                       return(
                           <Link to={item.link} className='' key={index}>
                                <li className='rankList-li' >
                                    <img src={item.subImg} className='rankList-img'/>
                                    <div className='title'>
                                        <p className='firstTitle'>{item.firstTitle}</p>
                                        <p className='subTitle'>{item.subTitle}</p>
                                        <p className='thirdTitle'>{item.thirdTitle}</p>
                                        <p className='lastTitle'>{item.lastTitle}</p>
                                    </div>
                                    <span className='detail'>{item.detail}</span>
                                </li>
                           </Link>
                       )
                   })}
               </ul>
           </div>
        )
    }

}
export default RankList


