import 'whatwg-fetch'
import 'es6-promise'

export function get(url){
    console.log(url);
    var result=fetch(url,{
        headers:{
            'Accept':'application/json,text/plain,*/*',
            'Access-Control-Allow-Methods':"POST,GET",
            'Access-Control-Allow-Origin':'*'
        }
    })
    return result
}


