import React from 'react'
import ReactSwipe from 'react-swipe'
import { Link } from 'react-router'

import './style.less'

class HomeSlider extends React.Component{
    constructor(props,context){
        super(props,context);
        this.state={
            index:0,
            imgUrl:[
                "邓紫棋",
                "周杰伦",
                "袁弘",
                "DJ"
            ]
        };
    }
    render(){
        const opt={
            auto:4000,
            callback:function(index){
                this.setState({index:index});
            }.bind(this)
        }
        return(
            <div id='home-slider'>
                <ReactSwipe className='swipe' swipeOptions={opt}>
                        {this.state.imgUrl.map((item,i)=>{
                            let imgObj=require(`../../static/images/slider/${i}.jpg`);
                            return (
                                    <Link to={`/search/${item}`}  key={i}>
                                        <img src={imgObj}  alt={item}/>
                                    </Link>
                            )
                        })}
                </ReactSwipe>
            
                <div className='home-slider-point'>
                    <ul>
                        <li className={this.state.index===0 ? 'selected' : ''}></li>
                        <li className={this.state.index===1 ? 'selected' : ''}></li>
                        <li className={this.state.index===2 ? 'selected' : ''}></li>
                        <li className={this.state.index===3 ? 'selected' : ''}></li>
                    </ul>
                </div>
            </div>
        )
        
    }
}
export default HomeSlider



