import React from 'react'
import PlayPage from '../../components/PlayPage'
import { getPlay }  from '../../fetch/play/'


class Play extends React.Component{
    constructor(props,context){
        super(props,context);
        this.state={
            data:[]
        }
    }
    render(){
        return(
            <PlayPage playData={this.state.data}/>
        )
    }
    getPlayData(){
        getPlay(this.props.params.junk)
        .then(res=>res.json())
        .then(json=>{
            console.log(json)
            this.setState({
                data:json.data
            })
        })
        .catch(ex=>console.error('搜索有误'+ex.message))
    }
    componentDidMount(){
        console.log('这是Play页面的componentDidMount')
        this.getPlayData()
    }
}
export default Play


