import { get } from '../get.js'

export function getSearch(moon){
    console.log(moon);
     var url='http://localhost:8002/api/search?',
         paramStr='';
   for(var p in moon){
         paramStr+=p+'='+moon[p];
        paramStr+='&';
   }
   url=url+paramStr.slice(0,-1);
    return get(url);
}


