import React from 'react'
import { Link } from 'react-router'

import './style.less'

class HomeTabs extends React.Component{
    constructor(props,context){
        super(props,context);
        this.state={
            tabList:[
                {
                    'name':'个性推荐',
                    'url':'/home'
                },
                {
                    'name':'歌手',
                    'url':'/singerList'
                },
                {
                    'name':'排行榜',
                    'url':'/rank'
                }
            ],
            today:0
        };
    //   this.onClickHandle=this.onClickHandle.bind(this);
    };
    render(){
    //    console.log(this.state)
        let tabData=this.state.tabList.map((item,index)=>{
            return(
                    <Link to={item.url} className='tab_item flex1'  key={index} activeClassName="selected">
                          <span  className="tab_link ">{item.name}</span>
                    </Link>
                
            )
        })
        return(
            <div className='tab flex'>
                {tabData}
            </div>
        )
    }
    
    clickHandle(i,e) {
        console.log(i);
        this.setState({"today":i});
        // console.log(this.state.index)
        // let Aspan = document.querySelectorAll('.tab_link')
        // console.log(Aspan)
    }
}
export default HomeTabs


