import React from 'react'
import SubDaySong from '../../components/DaySong'
import { getDaySong } from '../../fetch/singerList'

class DaySong extends React.Component{
    constructor(props,context){
        super(props,context);
        this.state={
            data:[]
        }
    }
    render(){
        // console.log(this.state.data)
        return(
            <SubDaySong  data={this.state.data}/>
        )
    }
    getDaySongData(){
        getDaySong()
        .then(res=>res.json())
        .then(json=>{
            // console.log(json)
            this.setState({
                data:json.data
            })
        })
        .catch(ex=>console.error('请求daySong数据出错'+ex.message))
    }
    componentDidMount(){
       this.getDaySongData()
    }

}

export default DaySong


