import React from 'react'
import { hashHistory } from 'react-router'
import './style.less'

const sq=require('../../static/images/sq.png')
class DaySong extends React.Component{
    constructor(props,context){
        super(props,context)
        this.state={
            title:'每日推荐'
        }
    }
    onClickHandle(){
        // 改路由
        window.history.back()
    }
    render(){
        const arrowLeft=require('../../static/images/arrow-left.png')
        const helpImg=require('../../static/images/daySong/help.png')
        const bgImg=require('../../static/images/daySong/bg.jpg')
        const daySongImg=require('../../static/images/daySong/daySong.png')
        return(
            <div className='daySong'>
                <div className='daySong-head'>
                    <img src={arrowLeft} className='daySong-head-arrowLeft' onClick={this.onClickHandle.bind(this)}/>
                    <span>{this.state.title}</span>
                    <img src={helpImg} className='daySong-head-helpImg'/>
                </div>
                <div className='daySong-middle'>
                    <img src={bgImg} className='daySong-middle-bg'/>
                    <img src={daySongImg} className='daySongImg'/>
                    <span className='daySong-date'>{(new Date()).getDate()}</span>
                </div>
                <div className='daySong-list'>
                   <ul className='daySong-list-ul'>
                      {
                          this.props.data.map((item,index)=>{
                              return (
                                    <li className='daySong-list-li' key={index}>
                                        <p className='songName'>{item.songName}</p>
                                        <img src={sq} className='sq'/>
                                        <p className='singer'>{item.singer}</p>
                                    </li>
                              )
                          })
                      }
                   </ul>
                </div>
            </div>
        )
    }
    // onChangeHandle(which){
    //     console.log(111)
    //     hashHistory.push(`/search/${which.songName}`)
    // }    
}
export default DaySong


