import React from 'react'
import HomeHead from '../../components/HomeHead'
import HomeTabs from '../../components/HomeTabs'
import RankList from '../../components/RankList'
import { getRank } from '../../fetch/rank'

class Rank extends React.Component{
    constructor(props){
        super(props);
        this.state={
            data:[]
        }
    }
    render(){
        // console.log(this.state.data)
        return (
            <div className='rank'>
                 <HomeHead />
                 <HomeTabs />
                 <RankList data={this.state.data} />
            </div>
           
        )
    }
    getRankData(){
        getRank()
        .then(res=>res.json())
        .then(json=>{
            // console.log(json.data)
            this.setState({
                data:json.data
            })
        })
    }
    componentDidMount(){
        console.log('componentDidMount被执行了')
        this.getRankData()
    }
}
export default Rank


