import React from 'react'
import { hashHistory } from 'react-router'
import SearchInput from '../SearchInput'

import './style.less'
class SearchHead extends React.Component{
    constructor(props,context){
        super(props,context)
    }
  
    render(){
        const arrLeft=require('../../static/images/arrow-left.png')
        return(
            <div className='search-head'>
                <div className='search-head-left'  onClick={this.onClickHandle.bind(this)}>
                    <img src={arrLeft} className='search-arrowleft'/>
                </div>
                <div className='search-head-input'>
                    <SearchInput value={this.props.value} />
                </div>
               
            </div>
        )
    }
    onClickHandle(){
        // 改路由
        console.log(111);
        window.history.back()
    }
}
export default SearchHead



